.infoOfProfile {
    display: flex;
    align-items: center;
    background-color: #ffe5e7;
    padding: 20px;
    border-radius: 20px;
    margin-bottom: 50px;
}

.avatar {
    margin-right: 30px;
}

.avatar img {
    width: 240px;
    height: 240px;
    border-radius: 20%;
    border: none;
}
